<template>
  <div class="home">
    <van-search
      v-model="value"
      shape="round"
      background="rgb(249,187,50)"
      input-align="center"
      placeholder="请输入搜索关键词"
      show-action
      @search="onSearch"
    >
      <template #left>
        <van-icon name="location-o" size="24" />
        <div class="address">{{address}}</div>
      </template>
      <template #action>
        <van-icon name="user-circle-o" size="24" />
        <!-- <van-icon name="cart-o" size="24"/> -->
      </template>
    </van-search>

    <div class="content">
      <!-- 轮播图 -->
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in bannerData" :key="index" @click="goDetail(item.pid)">
          <img class="imgs" :src="item.bannerImg" alt />
        </van-swipe-item>
      </van-swipe>

      <!-- 提示下单 -->
      <div class="tips">
        <van-button
          icon="https://s1.ax1x.com/2020/09/03/wiCrKe.png"
          color="rgb(249,187,50)"
          plain
          round
          @click="goMenu"
        >来一杯</van-button>
        <p>{{timeText}},亲爱的客人</p>
      </div>
      <!-- 推荐商品 -->
      <!-- <router-link to="/main/cart">gg</router-link> -->
      <div class="hot-product">
        <div class="hot-product-title">
          <van-icon class="hot" name="https://s1.ax1x.com/2020/09/03/wisSZ6.png" />热门推荐
          <van-icon class="hot" name="https://s1.ax1x.com/2020/09/03/wisSZ6.png" />
        </div>
        <div class="product-box">
          <!--  :class="[index % 2 == 0 ? 'p-left' : 'p-right']"-->
          <div class="product-item" v-for="(item, index) in hotProducts" :key="index">
            <div class="p-img" @click="goDetail(item.pid)">
              <img class="auto-img" :src="item.smallImg" alt />
            </div>

            <div class="pro-name one-text">{{item.name}}</div>
            <div class="prices">
              <div class="pro-price">￥{{item.price}}</div>
              <div
                :class="[item.isLike?'iconfont iconshoucang-copy': 'iconfont  iconshoucang']"
                @click="like(item,index)"
              ></div>
            </div>
          </div>

        </div>

      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/icons.css";
import '../../assets/css/home.less'
import $ from "jquery";
//导入命名空间 使用的是 menuModule
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      timeText: "",
      address: "",
      // banner轮播图数据 type类型数据
      bannerData: [],
      hotProducts: [],
      isLike: false,
      value:''
    };
  },
  created() {
    //调用 获取banner数据，type数据
    this.getAddress();
    this.getBanner();
    this.getTime();

    this.getHotProducts();
  },
  computed: {
    ...mapState(["bannerState"]),
  },
  methods: {
    ...mapMutations(["changeData", "changeProducts"]), //mutations提交commit改变数据
    getAddress() {
      $.ajax({
        type: "GET",
        url: "https://apis.map.qq.com/ws/location/v1/ip",
        data: {
          key: "ACTBZ-GTCRK-RPWJW-ACWO6-HHNZ5-57FE5",
          output: "jsonp",
        },
        dataType: "jsonp",

        success: (res) => {
          
          this.address = res.result.ad_info.city;
        },
      });
    },

    //获取banner数据
    getBanner() {
      //请求vuex的state中的bannerState轮播图数据。如果存在缓存数据就不用再重新请求vuex了
      if (this.bannerState.length > 0) {
        this.bannerData = this.bannerState;
        
        return;
      }

      //加载轻提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0, //延迟自动关闭加载提示，当为0不会自动关闭需要手动，在then解决关闭
      });

      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          //成功加载就清除 提示
          this.$toast.clear();
          
          if (result.data.code == 300) {
            //给bannerData数据赋值 之后在视图遍历字段bannerImg
            this.bannerData = result.data.result;

            //缓存banner数据在vuex中 menuModule下的方法changeData
            /* this.$store.commit('menuModule/changeData',{
              key:'bannerState',
              value:'result.data.result'
            })*/
            this.changeData({ key: "bannerState", value: result.data.result });
          }
        })
        .catch((err) => {
          //成功加载就清除 提示
          this.$toast.clear();
          
        });
    },
    //获取时间段
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好";
      } else {
        this.timeText = "深夜好";
      }
    },

    goMenu() {
      this.$router.push({ name: "Menu" });
    },
    //获取推荐商品数据
    getHotProducts() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 500) {
            
            result.data.result.forEach((v) => {
              v.isLike = false;
              this.findLike(v,v.pid);

            });

            this.hotProducts = result.data.result;

          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //查看商品详情
    goDetail(pid) {
      this.$router.push({ name: "Details", query: { pid } });
    },

    //查看商品是否被收藏 。 进入详情就可以看到
    findLike(v,pid) {
      
      let tokenString = localStorage.getItem("token");
      if(!tokenString){return}
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 1000) {
            // v.isLike = true;
            v.isLike = result.data.result.length > 0;
          } 
            //token无效或者 未登录 就跳转到登录页面
            // else {this.$router.push({ name: "Login" });}
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //是否收藏
    like(v, index) {
 
      let tokenString = localStorage.getItem("token");
      if(!tokenString){
        this.$toast('请先登录')
        // this.$router.push({ name: "Login" });
      }
      // let url = this.isLike ? "/notlike" : "/like";
      let url = '';

      if(v.isLike){
        url = '/notlike'
      }else{
         url = '/like'
      }

       this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: v.pid,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if(result.data.code == 800) {
            v.isLike = true;
          }else{
            v.isLike = false;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
        this.hotProducts[index].isLike = !this.isLike;
    },
    // 跳转搜索
    onSearch(keywords) {
      if(keywords==''){
        return;
      }
      keywords= this.value;
      
      this.$router.push({ name: "Search" ,query:{keywords}});
    },
  },
};
</script>

<style lang="less" scoped>

</style>